import { Box, HTMLChakraProps } from '@chakra-ui/react'
import React, { useEffect, useRef, useState } from 'react'

interface InputBoxProps extends HTMLChakraProps<'div'> {
  children: React.ReactNode
  focusBorder?: string
}

function InputBox({ children, focusBorder, ...rest }: InputBoxProps) {
  const [focused, setFocused] = useState(false)
  const boxRef = useRef(null)

  // 监听点击事件，判断是否点击了Box以外的区域
  useEffect(() => {
    const handleClickOutside = (e: any) => {
      // 如果点击的地方不在Box内部，才触发失去焦点
      if (boxRef.current && !boxRef.current.contains(e.target)) {
        setFocused(false)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
    return () => {
      document.removeEventListener('mousedown', handleClickOutside)
    }
  }, [])

  return (
    <Box
      ref={boxRef}
      w="100%"
      p="16px"
      border="1px solid"
      bg="bg_secondary"
      borderRadius="16px"
      onFocus={() => setFocused(true)}
      borderColor={focused ? (focusBorder ? focusBorder : 'token_active_border') : 'border'}
      boxShadow={focused ? '0px 0px 6px 0px #0067AD' : ''}
      {...rest}
    >
      {children}
    </Box>
  )
}

export default InputBox
